.registerHdng {
    font-weight: 700;
    margin-bottom: 30px;
    color: #274e13;
    text-align: center;
}

.registerForm {
    display: inline-block;
    text-align: left;
    /* border: 1px solid pink; */
}

label {
    font-weight: bold;
    color: #783f04
}

.registerBtn {
    padding: 3px 120px 3px 120px;
    margin-top: 10px;
    height: 45px;
    color: white;
    background-color: #274e13;
    font-weight: 500;
    font-size: 21px;
    border-radius: 8px;
    border: 2px solid #274e13;
}

.registerBtn:hover {
    color: #274e13;
    background-color: white;
    border: 1px solid #274e13;
}

input[type=number]::-webkit-inner-spin-button, input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}