.sales-container {
  /* border: 2px solid red; */
}
.returns-container {
  /* border: 2px solid red; */
}

.input-container {
  /* border: 2px solid red; */
  overflow: hidden;
  background-color: inherit;
}

.input-container div {
  margin: 1px 4px 0px 0px;
}

.input-container .label {
  margin: 0px 0px 0px 7px;
  font-weight: bold;
  color: #663300;
  font-size: 17px;
}

.input-container .input {
  padding: 2px 6px 2px 6px;
  color: #274e13;
  border: 1px solid #783f04;
}

.btn-container div {
  border: 2px solid #783f04;
}

.add-btn {
  margin-bottom: 20px;
  color: #ddd;
  text-align: center;
  font-size: 19px;
  font-weight: 700;
  background: #783f04;
  border: 2px solid #783f04;
  border-radius: 5px;
}


.add-btn:hover {
  background-color: #f2f2f2;
  color: #783f04;
  border: 2px solid #274e13;
}

.select {
  float: left;
  margin-right: 5px;
  padding: 6px;
  color: #783f04;
  text-align: center;
  font-size: 17px;
  font-weight: 600;
  background: #ddd;
  border: 1px solid #783f04;
  border-radius: 2px;
}

.checkout {
  float: right;
  /* border: 1px solid red; */
  font-size: 17px;
  color: #783f04;
  font-weight: 500;
}

.checkout button {
  box-sizing: border-box;
  padding: 0px 80px 0px 80px;
  margin-top: 5px;
  display: block;
  color: #ddd;
  text-align: center;
  font-size: 23px;
  font-weight: 700;
  background: #783f04;
  border: 2px solid #783f04;
  border-radius: 5px;
}

.checkout button:hover {
  background-color: #f2f2f2;
  color: #783f04;
  border: 2px solid #274e13;
}

.icon-container {
  /* border: 1px solid black; */
}

.iconSR {
  color: #274e13;
  margin-top: 40px;
  margin-bottom: 40px;
  font-size: 75px;
}

