
.customer-container{
  padding-top: 30px;
  /* border: 2px solid red; */
}

 .button-container {
  
  float: left;
  /* border: 2px solid black; */
  overflow: hidden;
  background-color: inherit;
} 


.button-container button{
  margin-left:3px;
  margin-right: 3px;
  float: left;
  display: block;
  color: #783f04;
  text-align: center;
  font-size: 23px;
  font-weight: 700;
  background: #ddd;
  border: 2px solid #783f04;
  border-radius: 5px;
}

.button-container button:hover{
  background-color: #f2f2f2;
  color: #274e13;
  border: 2px solid #274e13;
}

 .search-container {
   margin-right: 20px;
   padding: 0;
   /* margin-left: 80px; */
  float: right;
  /* border: 1px solid red; */
}
.select{
  float: left;
  margin-right: 5px;
  padding: 6px;
  color: #783f04;
  text-align: center;
  font-size: 17px;
  font-weight: 600;
  background: #ddd;
  border: 1px solid #783f04;
  border-radius: 2px;
}

.search-container input[type=text] {
  margin-left: 0px;
  padding: 6px;
  font-size: 17px;
  border: none;
  float: left;

}

.search-container button {
  float: right;
  padding: 6px 10px;
  background: #ddd;
  font-size: 17px;
  border: none;
  cursor: pointer;
  color: #783f04;

}

.search-container button:hover {
  background-color: #f2f2f2;
  color: #274e13;
}

.sale-btn{
  padding-left: 70px;
  padding-right: 70px;
  margin-top:5px;
  display: block;
  color: #ddd;
  text-align: center;
  font-size: 23px;
  font-weight: 700;
  background: #783f04;
  border: 2px solid #783f04;
  border-radius: 5px;
}
.vr{

  background-image: radial-gradient(#274e13, #999999);
	height: 400px;
  margin: 20px 10px 20px 30px;
	width: 6px;
}
  
.icon-container{
  color:#783f04;
  /* border: 2px solid #2feeff; */
  font-size: 35px;
  font-weight: 700;
  text-align: center;

}
.icon-customer{
  color: #274e13;
  margin-top: 40px;
  margin-bottom: 40px;
  font-size: 75px;

}


